import { Heading, Icon, List, ListIcon, ListItem, Stack, Text } from "@chakra-ui/react";
import React from "react";
import { ParagraphProps } from "../types";
import { MdArrowForward } from "react-icons/md";

function Paragraph ({title, text}: ParagraphProps) {
  return (
    <Stack spacing={6}>
      <Heading fontWeight={'extrabold'} textTransform='uppercase'>
        {title}
      </Heading>

      <List size='xl' spacing={4}>
        {
          text.map((string) => (
            <ListItem>
              <ListIcon as={MdArrowForward} />
              {string}
            </ListItem>
          ))
        }
      </List>
    </Stack>
  )
}

export default Paragraph