import React from "react"
import { HStack, Stack, Tag, TagLabel } from "@chakra-ui/react"

import { featuresTabs } from '../data/data'

function FeaturesTabs () {
	return (
    <Stack direction={'row'} spacing={4} wrap="wrap">
      {featuresTabs.map((feature) => (
        <Tag
          size='lg' borderRadius='full' variant='brand' minWidth={'fit-content'} 
          fontSize='lg'
        >
          <TagLabel>{feature}</TagLabel>
        </Tag>
      ))}
      
    </Stack>
  )
}

export default FeaturesTabs