import React from "react";
import { Box, Text, useBreakpointValue } from "@chakra-ui/react";
import './verticaltab.css'

function VerticalTab () {
	const responsive = useBreakpointValue(
    {
      base: 'mobile',
      xl: 'desktop',
    },
	)

	if (responsive === 'desktop') {
		return (
			<Box px={2} py={4} className="vertical-tab" borderRadius='full' alignContent='center' justifyContent='center'>
				<Text fontSize={'4xl'} className="vertical-tab-text" >
					Компетенции
				</Text>
			</Box>
		)
	}
	else {
		return (
			<Box px={8} py={2} mb={16} className="horizontal-tab" borderRadius='full' alignContent='center' justifyContent='center'>
				<Text fontSize={'2xl'} className="horizontal-tab-text">
					Компетенции
				</Text>
			</Box>
		)
	}
	
}

export default VerticalTab