import { featuresParagraphs } from "../data/data";

function OurFeatures () {
  return (
    <Grid my={10} p={8} templateColumns='repeat(10, 1fr)'>
      <GridItem colSpan={{base: 10, xl: 1}}>
        <VerticalTab />
      </GridItem>

      <GridItem colSpan={{base: 10, xl: 9}}>
        <Stack spacing={16}>
          {
            featuresParagraphs.map((paragraph) => (
              <Paragraph title={paragraph.title} text={paragraph.text} />
            ))
          }
        </Stack>
      </GridItem>
    </Grid>
  )
}

export default OurFeatures